"use client"
import { useState, useEffect } from "react";
import { ArrowLeft, CalendarClock, Clock, Users, AlertCircle } from "lucide-react";
import { ApolloClient, InMemoryCache, gql } from '@apollo/client';
import { toast, Toaster } from 'sonner';

// Components
import CampaignCountdown from "./countdown-timer"; 
import OverviewTab from "./overview-tab";
import RegisterTab from "./register-tab";
import VoteTab from "./vote-tab";
import ResultsTab from "./results-tab"; 

// UI Components

import { formatDate } from "@/lib/utils"; 
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

// GraphQL client
const client = new ApolloClient({
  uri: 'https://api.studio.thegraph.com/query/101223/polytix/version/latest',
  cache: new InMemoryCache(),
});

// GraphQL query
const CAMPAIGN_DETAILS_QUERY = gql`
  query CampaignDetails($id: String!) {
    campaignCreateds(where: {campaignId: $id}, first: 1) {
      creator
      votingType
      restriction
      startTime
      endTime
      blockTimestamp
    }
    campaignStatusChangeds(where: {campaignId: $id}, orderBy: blockTimestamp, orderDirection: desc, first: 1) {
      status
    }
    voteCasts(where: {campaignId: $id}) {
      voter
      targetId
    }
    voterRegistereds(where: {campaignId: $id}) {
      voter
    }
    candidateAddeds(where: {campaignId: $id}) {
      candidateId
      candidateAddress
      name
    }
    proposalAddeds(where: {campaignId: $id}) {
      proposalId
      content
    }
  }
`;

export default function CampaignDetailsPage() {
  // Simulate params and router from Next.js
  const params = { id: "1" }; // Mock campaign ID
  const router = { push: (path) => console.log(`Navigation to: ${path}`) };
  const campaignId = params.id;
  
  // Mock contract context
  const [address, setAddress] = useState("");
  const [isConnected, setIsConnected] = useState(false);
  const contract = null;
  const connectWallet = () => {
    setIsConnected(true);
    setAddress("0x1234...5678");
    toast.success("Wallet connected successfully");
  };
  const refetchEvents = () => console.log("Refetching events...");

  // State variables
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [campaignInfo, setCampaignInfo] = useState(null);
  const [proposals, setProposals] = useState([]);
  const [candidates, setCandidates] = useState([]);
  const [userRegistered, setUserRegistered] = useState(false);
  const [userVoted, setUserVoted] = useState(false);
  const [isOwner, setIsOwner] = useState(false);
  const [activeTab, setActiveTab] = useState("overview");

  // Time-based states
  const [isBeforeStart, setIsBeforeStart] = useState(false);
  const [isDuringVoting, setIsDuringVoting] = useState(false);
  const [isAfterEnd, setIsAfterEnd] = useState(false);

  // Fetch campaign data
  useEffect(() => {
    const fetchCampaignData = async () => {
      try {
        setLoading(true);
        setError(null);

        const { data } = await client.query({
          query: CAMPAIGN_DETAILS_QUERY,
          variables: { id: campaignId }
        });

        if (!data.campaignCreateds || data.campaignCreateds.length === 0) {
          setError("Campaign not found");
          setLoading(false);
          return;
        }

        const campaignCreatedEvent = data.campaignCreateds[0];
        const voteCasts = data.voteCasts || [];
        const voterRegistrations = data.voterRegistereds || [];
        const candidateData = data.candidateAddeds || [];
        const proposalData = data.proposalAddeds || [];
        const statusChanges = data.campaignStatusChangeds || [];

        // Check time-based states
        const now = Math.floor(Date.now() / 1000);
        const startTime = Number(campaignCreatedEvent.startTime);
        const endTime = Number(campaignCreatedEvent.endTime);

        setIsBeforeStart(now < startTime);
        setIsDuringVoting(now >= startTime && now < endTime);
        setIsAfterEnd(now >= endTime);

        // Check if user is owner
        if (isConnected && address) {
          const isCreator = campaignCreatedEvent.creator.toLowerCase() === address.toLowerCase();
          setIsOwner(isCreator);
        
          // Check if user is registered
          if (Number(campaignCreatedEvent.restriction) === 2) {
            const userRegistrationRecord = voterRegistrations.find(
              reg => reg.voter.toLowerCase() === address.toLowerCase()
            );
            setUserRegistered(!!userRegistrationRecord);
          } else {
            setUserRegistered(true);
          }
        
          // Check if user has voted
          const userVoteRecord = voteCasts.find(
            vote => vote.voter.toLowerCase() === address.toLowerCase()
          );
          setUserVoted(!!userVoteRecord);
        }

        // Process proposals or candidates
        if (Number(campaignCreatedEvent.votingType) === 1) {
          // Proposal-based
          const processedProposals = proposalData.map(proposal => {
            const voteCount = voteCasts.filter(vote => vote.targetId === proposal.proposalId).length;
            return {
              id: Number(proposal.proposalId),
              content: proposal.content,
              voteCount: voteCount
            };
          });
          setProposals(processedProposals);
        } else {
          // Candidate-based
          const processedCandidates = candidateData.map(candidate => {
            const voteCount = voteCasts.filter(vote => vote.targetId === candidate.candidateId).length;
            return {
              id: Number(candidate.candidateId),
              name: candidate.name,
              address: candidate.candidateAddress,
              statement: "Retrieved from subgraph",
              voteCount: voteCount
            };
          });
          setCandidates(processedCandidates);
        }

        // Combine all data
        const campaignData = {
          id: campaignId,
          votingType: Number(campaignCreatedEvent.votingType),
          restriction: Number(campaignCreatedEvent.restriction),
          resultType: 0,
          creator: campaignCreatedEvent.creator,
          description: `Campaign #${campaignId}`,
          startTime: Number(campaignCreatedEvent.startTime) * 1000,
          endTime: Number(campaignCreatedEvent.endTime) * 1000,
          status: statusChanges.length > 0 ? Number(statusChanges[0].status) : 0,
          totalVotes: voteCasts.length,
          itemCount: Number(campaignCreatedEvent.votingType) === 1 ? proposalData.length : candidateData.length,
          registeredVoterCount: voterRegistrations.length,
        };

        setCampaignInfo(campaignData);

        // Set initial active tab
        if (isAfterEnd) {
          setActiveTab("results");
        } else if (isDuringVoting && userRegistered && !userVoted) {
          setActiveTab("vote");
        } else if (isBeforeStart && campaignData.restriction !== 0 && !userRegistered) {
          setActiveTab("register");
        }

      } catch (err) {
        console.error("Error fetching campaign details:", err);
        setError("Failed to load campaign details. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    fetchCampaignData();
  }, [campaignId, address, isConnected]);

  // Handle registration success
  const handleRegistrationSuccess = async () => {
    setUserRegistered(true);
    toast.success("Successfully registered for the campaign!");
    
    // Update campaign stats
    setCampaignInfo((prev) => ({
      ...prev,
      registeredVoterCount: prev.registeredVoterCount + 1,
    }));
    
    refetchEvents();
    setActiveTab("overview");
  };

  // Handle vote success
  const handleVoteSuccess = async () => {
    setUserVoted(true);
    toast.success("Your vote has been cast successfully!");
    
    refetchEvents();
    
    // Update total votes
    setCampaignInfo((prev) => ({
      ...prev,
      totalVotes: prev.totalVotes + 1,
    }));
    
    setActiveTab("results");
  };

  // Get status message based on campaign state and user status
  const getStatusMessage = () => {
    if (!campaignInfo) return null;

    if (isAfterEnd) {
      return (
        <Alert className="bg-purple-50 dark:bg-purple-950/20 border-purple-200 dark:border-purple-900/50">
          <AlertDescription className="flex items-center">
            📊 This campaign has ended. Results are available below.
          </AlertDescription>
        </Alert>
      );
    }

    if (isBeforeStart) {
      if (campaignInfo.restriction === 0) {
        return (
          <Alert className="bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50">
            <AlertDescription className="flex items-center">
              🗳️ This campaign is open to anyone. You can cast your vote once the voting starts.
            </AlertDescription>
          </Alert>
        );
      } else if (!userRegistered) {
        return (
          <Alert className="bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50">
            <AlertDescription className="flex items-center">
              🔐 This campaign requires registration. Register now with the passkey to vote once voting starts.
            </AlertDescription>
          </Alert>
        );
      } else {
        return (
          <Alert className="bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50">
            <AlertDescription className="flex items-center">
              🎉 You are a registered voter in this campaign. All the best! You can view live results once voting
              starts.
            </AlertDescription>
          </Alert>
        );
      }
    }

    if (isDuringVoting) {
      if (userVoted) {
        return (
          <Alert className="bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50">
            <AlertDescription className="flex items-center">
              🙌 Thanks for voting using PolyTix. Your vote has been securely recorded.
            </AlertDescription>
          </Alert>
        );
      } else if (campaignInfo.restriction !== 0 && !userRegistered) {
        return (
          <Alert className="bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50">
            <AlertDescription className="flex items-center">
              🍿 You were not registered for this campaign. You can still view the results while grabbing some popcorn.
            </AlertDescription>
          </Alert>
        );
      } else {
        return (
          <Alert className="bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50">
            <AlertDescription className="flex items-center">
              🗳️ Voting is now open! Cast your vote before the deadline.
            </AlertDescription>
          </Alert>
        );
      }
    }

    return null;
  };

  if (loading) {
    return (
      <div className="container max-w-5xl mx-auto py-10 px-4 sm:px-6">
        <div className="space-y-6">
          <div className="flex items-center gap-2">
            <Skeleton className="h-6 w-32" />
          </div>
          <Skeleton className="h-12 w-full" />
          <Skeleton className="h-8 w-3/4" />
          <Skeleton className="h-24 w-full" />
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
          </div>
          <Skeleton className="h-64 w-full" />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container max-w-5xl mx-auto py-10 px-4 sm:px-6">
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
        <Button variant="outline" className="mt-4" onClick={() => router.push("/campaigns")}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Button>
      </div>
    );
  }

  if (!campaignInfo) {
    return (
      <div className="container max-w-5xl mx-auto py-10 px-4 sm:px-6">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>Campaign not found or still loading...</AlertDescription>
        </Alert>
        <Button variant="outline" className="mt-4" onClick={() => router.push("/campaigns")}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Button>
      </div>
    );
  }

  return (
    <div className="container max-w-5xl mx-auto py-10 px-4 sm:px-6">
      <Toaster position="top-right" richColors />
      
      {/* Back button */}
      <div className="mb-6">
        <Button
          variant="ghost"
          size="sm"
          className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground"
          onClick={() => router.push("/campaigns")}
        >
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Button>
      </div>

      {/* Campaign Title */}
      <div className="text-center mb-8 animate-in fade-in slide-in-from-top-5 duration-500">
        <h1 className="text-3xl font-bold uppercase tracking-tight">{campaignInfo.description}</h1>
        <div className="flex justify-center gap-2 mt-2">
          <Badge variant={isDuringVoting ? "default" : "outline"} className="animate-in fade-in zoom-in-95 delay-150">
            {isBeforeStart ? "Upcoming" : isDuringVoting ? "Active" : "Ended"}
          </Badge>
          <Badge variant="outline" className="animate-in fade-in zoom-in-95 delay-200">
            {campaignInfo.votingType === 0 ? "Candidate-Based" : "Proposal-Based"}
          </Badge>
          <Badge variant="outline" className="animate-in fade-in zoom-in-95 delay-250">
            {campaignInfo.restriction === 0
              ? "Open To All"
              : campaignInfo.restriction === 1
                ? "Limited"
                : "Registration Required"}
          </Badge>
        </div>
      </div>

      {/* Countdown Timer */}
      <Card className="mb-6 overflow-hidden animate-in fade-in slide-in-from-top-4 duration-500 delay-300">
        <CardContent className="pt-6 text-center">
          <h2 className="text-xl font-semibold mb-4">
            {isBeforeStart ? "Voting Starts In:" : isDuringVoting ? "Voting Ends In:" : "Campaign Ended"}
          </h2>
          {!isAfterEnd ? (
            <CampaignCountdown
              endTime={isBeforeStart ? campaignInfo.startTime : campaignInfo.endTime}
              className="max-w-md mx-auto"
            />
          ) : (
            <div className="text-lg font-medium text-muted-foreground">Final results are now available</div>
          )}
        </CardContent>
      </Card>

      {/* Status Message */}
      <div className="mb-6 animate-in fade-in slide-in-from-bottom-2 duration-500 delay-400">{getStatusMessage()}</div>

      {/* Campaign Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8 animate-in fade-in slide-in-from-bottom-2 duration-500 delay-500">
        <Card className="transition-all hover:shadow-md">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <CalendarClock className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Timeline</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>Start: {formatDate(campaignInfo.startTime)}</div>
                  <div>End: {formatDate(campaignInfo.endTime)}</div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="transition-all hover:shadow-md">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Users className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Participation</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>Total Votes: {campaignInfo.totalVotes}</div>
                  {campaignInfo.restriction !== 0 && <div>Registered Voters: {campaignInfo.registeredVoterCount}</div>}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="transition-all hover:shadow-md">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Clock className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Status</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>{isBeforeStart ? "Registration Phase" : isDuringVoting ? "Voting Active" : "Voting Ended"}</div>
                  {isConnected ? (
                    <div>
                      {userRegistered ? `Registered: ${userVoted ? "Voted" : "Not voted yet"}` : "Not registered"}
                    </div>
                  ) : (
                    <div>Connect wallet to check status</div>
                  )}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Tabs */}
      <Tabs value={activeTab} onValueChange={setActiveTab} className="animate-in fade-in slide-in-from-bottom-1 duration-500 delay-600">
        <TabsList className="grid grid-cols-2 md:grid-cols-4 mb-6">
          <TabsTrigger value="overview" className="transition-all">Overview</TabsTrigger>
          {isBeforeStart && campaignInfo.restriction !== 0 && !userRegistered && (
            <TabsTrigger value="register" className="transition-all">Register</TabsTrigger>
          )}
          {isDuringVoting && (campaignInfo.restriction === 0 || userRegistered) && !userVoted && (
            <TabsTrigger value="vote" className="transition-all">Vote</TabsTrigger>
          )}
          {(isDuringVoting || isAfterEnd || isOwner) && (
            <TabsTrigger value="results" className="transition-all">Results</TabsTrigger>
          )}
        </TabsList>

        <TabsContent value="overview" className="animate-in fade-in duration-300 ease-in-out">
          <OverviewTab
            campaignInfo={campaignInfo}
            isOwner={isOwner}
            isBeforeStart={isBeforeStart}
            isDuringVoting={isDuringVoting}
            isAfterEnd={isAfterEnd}
            proposals={proposals}
            candidates={candidates}
          />
        </TabsContent>

        {isBeforeStart && campaignInfo.restriction !== 0 && !userRegistered && (
          <TabsContent value="register" className="animate-in fade-in duration-300 ease-in-out">
            <RegisterTab
              campaignInfo={campaignInfo}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected}
              connectWallet={connectWallet}
              onRegistrationSuccess={handleRegistrationSuccess}
            />
          </TabsContent>
        )}

        {isDuringVoting && (campaignInfo.restriction === 0 || userRegistered) && !userVoted && (
          <TabsContent value="vote" className="animate-in fade-in duration-300 ease-in-out">
            <VoteTab
              campaignInfo={campaignInfo}
              proposals={proposals}
              candidates={candidates}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected}
              connectWallet={connectWallet}
              onVoteSuccess={handleVoteSuccess}
            />
          </TabsContent>
        )}

        {(isDuringVoting || isAfterEnd || isOwner) && (
          <TabsContent value="results" className="animate-in fade-in duration-300 ease-in-out">
            <ResultsTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} isOwner={isOwner} />
          </TabsContent>
        )}
      </Tabs>
    </div>
  );
}